import React, { useState, useRef } from 'react';
import { Modal, Image, Form } from 'semantic-ui-react';
import './index.scss';
import PropTypes from 'prop-types';
import Thumbnail from 'components/common/Thumbnail';
import cameraIcon from 'assets/images/camera-icon.png';
import ModalHeader from '../ModalHeader';

const EditChatModal = ({
  editOpen,
  setEditOpen,
  onSetNewDetails,
}) => {
  const [form, setForm] = useState({});
  const imageInputRef = useRef(null);

  const onChange = (e, { name, value }) => {
    setForm({ ...form, [name]: value });
  };

  const onSubmit = () => {
    onSetNewDetails(form);
  };
  return (
    <Modal
      open={editOpen}
      size="mini"
      onClose={() => {
        setEditOpen(false);
      }}
      className="chooser-modal"
    >
      <ModalHeader
        setOpen={() => setEditOpen(false)}
        title={global.translate('Edit', 820)}
        confirmContent={global.translate('Save', 614)}
        onConfirmClick={() => {
          onSubmit();
          setEditOpen(false);
        }}
      />
      <Modal.Content>
        <div className="avatar-image">
          <Thumbnail
            avatar="data:image/jpeg;base64,"
            size="medium"
            name="A"
            secondName="A"
            circular
            className="header_2u_avatar"
            style={{
              height: '95px',
              width: '95px',
              margin: 'auto',
              objectFit: 'cover',
            }}
          />
          <div className="camera-input">
            <input
              type="file"
              accept="image/*"
              ref={imageInputRef}
              onChange={onChange}
              style={{ display: 'none' }}
            />
            <Image
              src={cameraIcon}
              width={18}
              onClick={() => imageInputRef.current.click()}
            />
          </div>
        </div>

        <Form size="tiny">
          <Form.Group widths="equal">
            <Form.Input
              name="GroupName"
              onChange={onChange}
              label={global.translate('Group Name')}
              control="input"
              placeholder={global.translate('Group Name')}
            />
          </Form.Group>
        </Form>
      </Modal.Content>
    </Modal>
  );
};

EditChatModal.propTypes = {
  editOpen: PropTypes.bool,
  setEditOpen: PropTypes.func,
  onSetNewDetails: PropTypes.func,
};

EditChatModal.defaultProps = {
  editOpen: false,
  setEditOpen: () => {},
  onSetNewDetails: () => {},
};

export default EditChatModal;
